.split-container {
    width: 100%;
}

.split {
    display: flex;
    padding-right: 20px;
}

.panel {
    height: 88vh; /* Занимает всю доступную высоту */
    overflow: auto;
    padding: 20px;
    padding: 10px;
}

.panel2::-webkit-scrollbar {
    width: 0; /* For Chrome, Safari, Opera */
}

.gutter {
    cursor: col-resize; /* Тип курсора для вертикального разделения */
    width: 100%; /* Занимает всю доступную ширину */
    position: relative;
    z-index: 1;

    transition: 0.3s ease-in-out all;
    background-color: #ddd;

    &::after {
        content: "";
        width: 30px;
        height: 30px;

        position: absolute;
        top: 50%;
        left: -12px;
        background: url("../../assets/icon/resize.svg") no-repeat center center;
        overflow: visible;
    }

    @media (any-hover: hover) {
        &:hover {
            background-color: #bbb; /* Измененный цвет фона при наведении */
            width: 8px;
        }
    }
}
