.addresses {
    // .addresses__content
    &__content {
        margin-top: 20px;
    }
    // .addresses__list
    &__list {
        display: flex;
        flex-direction: column;
        gap: 25px;
    }
    // .addresses__city
    &__city {
        min-height: 100%;
    }
    // .addresses__title
    &__title {
        margin-bottom: 10px;
        font-size: 25px;
    }
    // .addresses__addresses-container
    &__addresses-container {
        display: flex;
        align-content: flex-start;
        flex-wrap: wrap;
        gap: 7px;
    }
    // .addresses__street
    &__street {
        padding: 7px 10px;
        border: 1px solid black;
        color: #000;
        min-height: 40px;

        border-radius: 10px;
        cursor: grab;

        transition: 0.15s ease-in all;

        @media (any-hover: hover) {
            &:hover {
                transform: scale(1.03);
            }
        }
    }
    &__no-addresse {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 60vh;
    }
    &__left-top {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }
    &__left-selects {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 15px;
        flex: 0 1 auto;
    }
}
