@import "variables";
@import "fonts";
@import "_vendor/reset";
@import "components/all";

body {
	background-color: #fff;
	font-weight: 400;
	font-family: "Source Sans Pro", sans-serif;
	color: $text;
}

.container {
	max-width: $container-width;
	padding: 0px 15px;
	margin: 0 auto;
}

#root {
	scrollbar-width: none;
}

body::-webkit-scrollbar {
	width: 0; /* For Chrome, Safari, Opera */
}

.main-layout {
	display: flex;
	flex-direction: column;
	grid-column: 2 / 3;
	grid-row: 2 / 3;
}

.app {
	display: flex;
	position: relative;
	min-height: 100vh;
}

.content {
	width: 100%;
}

::-webkit-scrollbar {
	width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #e0e0e0;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #555;
}
